a {
  text-decoration: none;
  color: black; }

.landing {
  grid-template-columns: repeat(10, 10%);
  grid-template-rows: repeat(10, 10%);
  display: grid;
  width: 100vw;
  position: relative;
  overflow: scroll;
  height: 200vw;
  background: #9d978069; }

.sidebar {
  width: 30vw;
  height: 75vh;
  position: fixed;
  left: 2vh;
  top: 3vw;
  display: inline-flex;
  align-items: center;
  justify-content: center; }

.sidebar-Wrapper {
  display: grid;
  transform: rotate(-90deg);
  flex-direction: row-reverse;
  grid-template-columns: repeat(8, 12.5%);
  grid-template-rows: repeat(8, 12.5%);
  width: 100%;
  height: 15%;
  margin-left: -25%; }

.contact {
  font-size: 2vw;
  margin: 0.67em 0;
  grid-area: 1/9/1/9;
  transform: rotate(90deg);
  font-family: raleway;
  border-top: solid black 0.5vw;
  margin-top: 0vw; }

.a__linkedIn {
  grid-area: 5/1/5/1;
  margin: 0 auto; }

.a__instagram {
  grid-area: 5/3/5/3;
  margin: 0 auto; }

.a__github {
  grid-area: 5/5/5/5;
  margin: 0 auto; }

.resumeText {
  grid-area: 5/7/5/7;
  text-align: center; }

.titles {
  font-size: 1.3vw;
  height: 2rem;
  cursor: pointer;
  color: black;
  margin: 0 auto;
  text-transform: lowercase;
  font-family: raleway; }

.icons {
  position: relative;
  width: 100%;
  display: inherit;
  grid-area: 1/1/3/9;
  grid-template-columns: repeat(8, 12.5%);
  grid-template-rows: repeat(8, 12.5%);
  margin-top: -25%; }
  .icons .icons__resume {
    cursor: pointer;
    grid-area: 4/7/4/7;
    width: 4.3vw;
    transform: rotate(0deg);
    margin: 0 auto;
    padding: 0; }
  .icons .grid__linkedIn {
    grid-area: 4/1/4/1;
    margin: 0 auto; }
    .icons .grid__linkedIn .square {
      display: flex;
      width: 5.5vw;
      transform: rotate(0deg);
      margin: 0 auto;
      padding: 0;
      filter: grayscale(1) brightness(0); }
    .icons .grid__linkedIn .a__icon-linkedIn {
      width: 30vh;
      transform: rotate(0deg);
      margin: 0 auto;
      padding: 0;
      display: none; }
  .icons .grid__instagram {
    grid-area: 4/3/4/3;
    margin: 0 auto; }
    .icons .grid__instagram .a__icon-instagram {
      width: 5.5vw;
      transform: rotate(0deg);
      margin: 0 auto;
      padding: 0; }
  .icons .grid__github {
    grid-area: 4/5/4/5;
    margin: 0 auto; }
    .icons .grid__github .a__icon-github {
      width: 5.5vw;
      transform: rotate(0deg);
      margin: 0 auto;
      padding: 0; }

.icons__click {
  border-style: none;
  width: 4vw;
  margin: 2.4vw 1vw;
  transform: rotate(-90deg);
  cursor: pointer; }

.icons__linkedIn {
  width: 8vw;
  margin-left: -1vw;
  margin-top: -2.3vw;
  padding-left: 3rem;
  padding-bottom: 2rem;
  filter: grayscale(1) brightness(0); }

.logo {
  width: 20vw;
  height: 19vw;
  margin-top: 7vh;
  -webkit-clip-path: polygon(50% 0, 0% 100%, 100% 100%);
  clip-path: polygon(50% 0, 0% 100%, 100% 100%);
  background: black;
  border: black solid;
  position: fixed;
  right: 3vh; }

.name {
  grid-area: 2/3/2/7;
  font-family: Raleway;
  font-size: 2vw;
  margin-left: 2vw; }

.quote {
  grid-area: 1/6/1/9;
